ds / 1800 - 0.5) * Math.PI, false);\r\n\t\t\t\t\t\tctx.stroke();\r\n\r\n\t\t\t\t\t\tctx.beginPath();\r\n\t\t\t\t\t\tctx.strokeStyle = \"rgb(0, 0, 255)\";\r\n\t\t\t\t\t\tctx.shadowColor = \"rgba(128, 128, 255, 0.5)\";\r\n\t\t\t\t\t\tctx.arc(100, 100, 60, startAngle, (seconds / 30 - 0.5) * Math.PI, false);\r\n\t\t\t\t\t\tctx.stroke();\r\n\r\n\t\t\t\t\t\ttime = [];\r\n\t\t\t\t\t\tif (hours < 10) {\r\n\t\t\t\t\t\t\ttime.push('0');\r\n\t\t\t\t\t\t}\r\n\t\t\t\t\t\ttime.push(hours);\r\n\t\t\t\t\t\ttime.push(':');\r\n\r\n\t\t\t\t\t\tif (minutes < 10) {\r\n\t\t\t\t\t\t\ttime.push('0');\r\n\t\t\t\t\t\t}\r\n\t\t\t\t\t\ttime.push(minutes);\r\n\t\t\t\t\t\ttime.push(':');\r\n\r\n\t\t\t\t\t\tif (seconds < 10) {\r\n\t\t\t\t\t\t\ttime.push('0');\r\n\t\t\t\t\t\t}\r\n\t\t\t\t\t\ttime.push(seconds);\r\n\r\n\t\t\t\t\t\tif (am) {\r\n\t\t\t\t\t\t\ttime.push('AM');\r\n\t\t\t\t\t\t} else {\r\n\t\t\t\t\t\t\ttime.push('PM');\r\n\t\t\t\t\t\t}\r\n\r\n\t\t\t\t\t\tctx.fillText(time.join(''), 50, 105);\r\n\t\t\t\t\t}\r\n\t\t\t\t\tdrawClock();\r\n\t\t\t\t\tsetInterval(drawClock, 1000);\r\n\t\t\t\t}\r\n\t\t\t}\r\n\t\t},\r\n\t}\r\n</script>\r\n\r\n<style>\r\n\t#clockCanvas{\n\t\twidth: 12.5rem;\n\t\theight: 12.5rem;\n\t}\r\n</style>\n"],"sourceRoot":""}]);
